import React, { Component } from 'react';
import { connect } from 'react-redux';

import AppHeader from '../app-header';
import TodoNewItem from '../todo-new-item';
import Todolist from '../todo-list';
import TodoFilter from '../todo-filter';

import './app.css';

let id = 0;

class App extends Component {
  addTask = () => {
    const todoInput = document.querySelector('.new-item-input');
    let label = todoInput.value;

    this.props.onAddTask({ label, id });
    id++;
    todoInput.value = '';
  };

  render() {
    console.log(this.props.store);
    return (
      <div className='app-container'>
        <AppHeader />
        <form className='todo-form' action='#' onSubmit={this.addTask}>
          <TodoNewItem />
          <Todolist todos={this.props.store} />
          <TodoFilter todos={this.props.store}/>
        </form>
      </div>
    );
  };
};

export default connect(
  state => ({
    store: state
  }),
  dispatch => ({
    onAddTask: (task) => {
      dispatch({ type: 'ADD_NEW_ITEM', payload: task });
    }
  })
)(App);