import React from 'react';

import './todo-filter.css';

const TodoFilter = ({ todos }) => {
  return (
    <div className='filter'>
      <div className='container'>
        <button className='filter__btn' id='check-all-btn' type='button'>{todos.length} tasks left</button>
        <ul className='filter__list'>
          <li className='filter__item'>
            <input className='filter__input visually-hidden' type='radio' name='filter' id='all'/>
            <label className='filter__label' htmlFor='all'>All</label>
          </li>
          <li className='filter__item'>
            <input className='filter__input visually-hidden' type='radio' name='filter' id='todo'/>
            <label className='filter__label' htmlFor='todo'>Todo</label>
          </li>
          <li className='filter__item'>
            <input className='filter__input visually-hidden' type='radio' name='filter' id='completed'/>
            <label className='filter__label' htmlFor='completed'>Completed</label>
          </li>
        </ul>
        <button className='filter__btn' id='clear-completed-btn' type='button'>Clear completed</button>
      </div>
    </div>
  );
};

export default TodoFilter;